@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Beth+Ellen&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html, body, #root {
  height: 100%;
}

body {
  background: #F5F5F5;
}

body, input, button {
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

.login-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-container article {
  width: 100%;
  max-width: 350px;
  display: flex;
  flex-direction: column;
}

.login-container form input {
  margin-top: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  height: 48px;
  padding: 0 20px;
  font-size: 16px;
  color: #666;
}

.login-container form input::-webkit-input-placeholder {
  color: #999;
}

.login-container form input::-moz-placeholder {
  color: #999;
}

.login-container form input:-ms-input-placeholder {
  color: #999;
}

.login-container form input::-ms-input-placeholder {
  color: #999;
}

.login-container form input::placeholder {
  color: #999;
}

.login-container a {
  margin-top: 20px;
  border: 0;
  border-radius: 4px;
  height: 48px;
  font-size: 1.5em;
  background-color: #DF4723;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-weight: bold;
  color: #FFF;
  cursor: pointer;
}
.main-container {
  max-width: 980px;
  margin: 0 auto;
  padding: 50px 0;
  text-align: center;
}

.main-container ul {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
  margin-top: 50px;
}

.main-container ul li {
  display: flex;
  flex-direction: column;
}

.main-container ul li img {
  max-width: 100%;
  border-radius: 5px 5px 0 0;
}

.main-container ul li footer {
  flex: 1 1;
  background-color: #FFF;
  border: 1px solid #EEE;
  padding: 15px 20px;
  text-align: left;
  border-radius: 0 0 5px 5px;
}

.main-container ul li footer strong {
  font-size: 16px;
  color: #333;
}

.main-container ul li footer strong span {
  font-size: 12px;
  margin-left: 5px;
  color: #aaa;
}

.main-container ul li footer p {
  font-size: 14px;
  line-height: 20px;
  color: #999;
  margin-top: 5px;
}

.main-container ul li .buttons {
  margin-top: 10px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
}

.main-container ul li .buttons button {
  height: 50px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.05);
  border: 0;
  border-radius: 4px;
  background-color: #FFF;
  cursor: pointer;
  -webkit-transition: all .2s;
  transition: all .2s;
}

.main-container ul li .buttons button:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.main-container .empty {
  font-size: 32px;
  color: #999;
  font-weight: bold;
  height: 100%;
  margin-top: 100px;
}

@media (max-width: 900px) {
  .main-container ul {
    grid-template-columns: repeat(2, 1fr);
    margin: 1em;
  }
}

@media (max-width: 414px) {
  .main-container ul {
    grid-template-columns: 1fr;
  }
}
.match-container {
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.match-container strong {
  margin-top: 20px;
  font-size: 25px;
}

.match-container button {
  border: none;
  background: none;
  font-weight: bold;
  color: rgba(255, 255, 255, 0.8);
  font-size: 18px;
  margin-top: 30px;
  cursor: pointer;
  text-transform: uppercase;
}

.match-title {
  font-size: 50px;
  font-family: 'Beth Ellen', cursive;
  font-weight: bold;
}

.match-image img {
  max-height: 200px;
  border-radius: 50%;
  border: 4px solid #FFF;
}

.match-bio {
  margin-top: 10px;
  font-size: 20px;
  line-height: 30px;
  max-width: 400px;
  color: rgba(255, 255, 255, 0.8);
}
.toggle-button {
  background: transparent;
  height: 18px;
  width: 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border: none;
  cursor: pointer;
  box-sizing: border-box;
}

.toggle-button:focus {
  outline: none;
}

.toggle-button-line {
  width: 25px;
  height: 2px;
  background-color: #FFF;
}
.toolbar {
  position: fixed;
  width: 100%;
  height: 56px;
  top: 0;
  left: 0;
  background-color: #222;
}

.toolbar nav {
  display: flex;
  flex-direction: row;
  height: 100%;
  align-items: center;
  padding: 0 2rem;
}

.spacer {
  flex: 1 1;
}

.toolbar-logo {
  margin-left: 1rem;
}

.toolbar-items ul {
  list-style: none;
  display: flex;
}

.toolbar-items li {
  padding: 0 0.5rem;
}

.toolbar-items a {
  padding: 10px;
  color: #FFF;
  text-decoration: none;
  font-size: 1.2em;
  text-transform: uppercase;
  -webkit-transition: all .2s;
  transition: all .2s;
}

.toolbar-items a:hover,
.toolbar-items a:active {
  background-color: #444;
}

@media (max-width: 768px) {
  .toolbar-items {
    display: none;
  }
}

@media (min-width: 769px) {
  .toolbar-toggle-button {
    display: none;
  }
}
.drawer {
  height: 100vh;
  background-color: #222;
  box-shadow: 2px 0 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 400px;
  z-index: 200;
  -webkit-transform: translateX(-110%);
          transform: translateX(-110%);
  -webkit-transition: -webkit-transform 0.5s ease-out;
  transition: -webkit-transform 0.5s ease-out;
  transition: transform 0.5s ease-out;
  transition: transform 0.5s ease-out, -webkit-transform 0.5s ease-out;
}

.drawer.open {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
}

.drawer li {
  margin: 2rem;
}

.drawer li a {
  padding: 10px;
  color: #FFF;
  font-size: 1.2rem;
  text-transform: uppercase;
  text-decoration: none;
  -webkit-transition: all .2s;
  transition: all .2s;
}

.drawer li a:hover,
.drawer li a:active {
  background-color: #444;
}

@media (min-width: 769px) {
  .drawer {
    display: none;
  }
}
.backdrop {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 100;
  display: none;
}

.backdrop.open {
  display: block;
}
.page-404-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  text-align: center;
}

.page-404-header {
  max-width: 700px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
  align-items: center;
}

.page-404-header h1 {
  font-size: 140px;
  color: #DF4723;
  font-weight: bold;
}

.page-404-header h3 {
  color: #777;
  font-size: 25px;
}

.page-404-description {
  max-width: 700px;
  line-height: 30px;
  color: #777;
  font-size: 18px;
}

.page-404-description code {
  font-size: 16px;
  background-color: #333;
  padding: 3px 5px;
  border-radius: 5px;
  color: yellow;
}

.go-back {
  margin-top: 5vh;
  color: #DF4723;
  text-decoration: none;
  font-weight: bold;
  text-transform: uppercase;
}

.go-back:active {
  color: #ff0000
}

@media (max-width: 800px) {
  .page-404-container {
    padding: 10px;
  }

  .page-404-header {
    grid-template-columns: 1fr;
  }
}
.matches-container {
  max-width: 980px;
  margin: 0 auto;
  padding: 50px 0;
  text-align: center;
}

.matches-container article {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 1fr;
  grid-gap: 30px;
  margin-top: 50px;
}

.matches-container article a {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: black;
}

.matches-container article a footer {
  flex: 1 1;
  background-color: #FFF;
  border: 1px solid #EEE;
  padding: 15px 20px;
  text-align: left;
  border-radius: 0 0 5px 5px;
}

.matches-container article a footer strong {
  font-size: 16px;
  color: #333;
}

.matches-container article a footer strong span {
  font-size: 12px;
  margin-left: 5px;
  color: #aaa;
}

.matches-container article a footer p {
  font-size: 14px;
  line-height: 20px;
  color: #999;
  margin-top: 5px;
}

.matches-container article img {
  max-width: 100%;
  height: 40vh;
  border-radius: 5px 5px 0 0;
}

.matches-container .empty-matches {
  font-size: 32px;
  color: #999;
  font-weight: bold;
  height: 100%;
  margin-top: 100px;
}

@media (max-width: 900px) {
  .matches-container article {
    grid-template-columns: repeat(2, 1fr);
    margin: 1em;
  }
}

@media (max-width: 414px) {
  .matches-container article {
    grid-template-columns: 1fr;
  }
}
